  margin-top: 2%;
  background-color: green; 
  /* display: block; */
}

.DisplayFlex {
  display: flex;
  flex-direction: row;
  padding: 50px;
}

.matrixA {
  margin-right: 20px;
}

.matrixB {
  margin-left: 20px;
