.header {
  border: 2px solid black;
  width: 700px;
  text-align: center;
  font-size: 70%;
  justify-content: center;
}

.home {
  display: flex;
  flex-direction: row;
  flex-direction: row;
  grid-gap: 10px;
  gap: 10px;
  grid-column-gap: 10px;
  -webkit-column-gap: 10px;
          column-gap: 10px;
  margin-top: 4%;
  justify-content: center;
}

.block {
  width: 100%;
  height: 600px;
  border: 2px solid black;
  font-size: 20px;
}

.border {
  border: 2px solid black;
  width: 50%;
  height: 100%;
}

body {
  background: #f2f2f2;
}

.container  {
  position: absolute;
  left: 50%;
  top: 50%;
  -webkit-transform: translate(-50%, -50%);
          transform: translate(-50%, -50%);
  height: 400px;
  width: 600px;
  background: #f2f2f2;
  overflow: hidden;
  border-radius: 20px;
  cursor: pointer;
  box-shadow: 0 0 20px 8px #d0d0d0;
}

.content {
  position: absolute;
  top: 50%;
  -webkit-transform: translatey(-50%);
          transform: translatey(-50%);
  text-align: justify;
  color: black;
  padding: 40px;
  font-family: 'Merriweather', serif;
}

h1 {
  font-weight: 900;
}

h3 {
  font-weight: 300;
}

.flap {
  width: 100%;
  height: 100%;
}

.center {
  align-content: center;
}

.flap::before {
  position: absolute;
  content: "";
  height: 100%;
  width: 50%;
  background: url("https://pbs.twimg.com/profile_images/1347260174176710658/2GfSZ1i__400x400.jpg") white;
  background-position: 100px;
  background-repeat: no-repeat;
  transition: 1s;
}

.flap::after {
  position: absolute;
  content: "";
  height: 100%;
  width: 50%;
  right: 0;
  background: url("https://pbs.twimg.com/profile_images/1347260174176710658/2GfSZ1i__400x400.jpg") white;
  background-position: -200px;
  background-repeat: no-repeat;
  transition: 1s;
}

.container:hover .flap::after {
  -webkit-transform: translatex(300px);
          transform: translatex(300px);
}

.container:hover .flap::before{
  -webkit-transform: translatex(-300px);
          transform: translatex(-300px);
}

.input_matrix {
  width: 100px;
}

.input_size {
  width: 50px;
}
